<template>
  <div class="goods">
    <p>精品优选</p>
    <van-row type="flex" justify="space-between"  tag="ul">
      <van-col span="6"
               v-for="item of list"
               :key="item.id"
               tag="li"
      >
        <a href="##">
          <img src="../images/ad1.jpg">
          <div>{{item.title}}</div>
          <div>
            <span>￥{{item.price}}</span>
            <span>{{item.buy}}人购买</span>
          </div>
        </a>
      </van-col>
    </van-row>
  </div>
</template>

<script>
  export default {
    name: "Goods",
    data(){
      return{
        list:[]
      }
    },
    mounted() {
      axios({
        url:'/api/home',
        params:{_page:1,_limit:6}
      }).then((res)=>{
        this.list = res.data.data
        console.log(res.data.data)
      })
    }
  }
</script>
<style scoped>
  .goods{padding-top:10px;}
  .goods p{text-align: center}
  .goods ul{flex-wrap: wrap;padding:0 10px 10px 0;}
  .goods ul li{width:50%;padding:10px 0 0 10px;}
  .goods ul li img{width:100%;}
</style>