<template>
  <div class="user">
    <div class="user-t">个人中心</div>
    <div class="user-c1">
      <a href="##">
        <img src="../images/ad1.jpg">
        <div class="c">
          <span>{{data.nikename}}</span>
          <p>时尚女王</p>
        </div>
        <van-icon name="arrow" size="24px" tag="b"/>
      </a>
    </div>
    <div class="user-c2">
      <a @click="logout">注销登录</a>
    </div>
  </div>
</template>

<script>
  import { Toast } from 'vant';
  Vue.use(Toast);
  import { Icon } from 'vant';
  Vue.use(Icon);
  import Vue from 'vue'
  export default {
    name: "User",
    data(){
      return {
        data:{}
      }
    },
    beforeRouteEnter(to,from,next){
      axios({
        url:'/api/user'
      }).then((res)=>{
        if(res.data.err === 0){
          next((_this)=>{
            _this.data = res.data.data
          })
        }else{
          next('/login')
          console.log(res)
        }
      })
    },
    methods:{
      logout(){
        axios({
          url:'/api/logout',
          method:'put'
        }).then((res)=>{
          if(res.data.err === 0){
            Toast('注销成功');
             this.$router.push('/login')
          }
        })
      }
    }
  }
</script>

<style scoped>
  .user{}
  .user-t{text-align:center;font-size:18px;padding:10px 0;background:#f7f7f7;}
  .user-c1{}
  .user-c1 a{display: flex;padding:10px;}
  .user-c1 a img{width:40px;height:42px;margin-right:8px;}
  .user-c1 a .c{padding-right:64%;}
  .user-c1 a .c span{font-size: 14px;line-height: 20px;}
  .user-c1 a .c p{font-size: 14px;margin-top:-1px;color:#999;}
  .user-c1 a b{margin-top:10px;}
  .user-c2{width:100%;text-align: center;}
  .user-c2 a{display: block;line-height: 40px;color:#ff4400;background:#f7f7f7;}
</style>