<template>
  <div class="reg">
    <div class="reg-t">
      <van-icon name="arrow-left" size="24px" tag="i" onclick="window.history.go(-1)"/>
      <span>注册</span>
      <van-icon name="setting" size="24px" tag="b"/>
    </div>
    <div class="reg-c">
      <van-field
          v-model="username"
          clearable
          label="用户名:"
          right-icon="question-o"
          placeholder="请输入用户名"
      />
      <van-field
          v-model="password"
          type="password"
          label="密码:"
          placeholder="请输入密码"
      />
      <van-field
          v-model="password2"
          type="password"
          label="确认密码:"
          placeholder="请输入密码"
      />
      <van-field
          v-model="sms"
          center
          clearable
          label="短信验证码"
          placeholder="请输入短信验证码"
      >
        <van-button slot="button" size="small" type="primary">发送验证码</van-button>
      </van-field>
    </div>

    <div class="reg-b">
      <van-button type="danger" tag="p" @click="reg">立即登录</van-button>

      <a @click="login">已有账号 登录</a>

    </div>
    <span v-model="mess">{{mess}}</span>
  </div>
</template>

<script>
  import { Toast } from 'vant';

  Vue.use(Toast);
  import { Icon } from 'vant';
  Vue.use(Icon);
  import { Field } from 'vant';
  Vue.use(Field);
  import Vue from 'vue'

  export default {
    name: "Reg",
    data(){
      return {
        username:'',
        password:'',
        password2:'',
        sms:'',
        mess:''
      }
    },
    methods:{
      login(){
        this.$router.push('/login')
      },
      reg(){
        axios({
          url:'/api/reg',
          method:'post',
          data:{username:this.username,password:this.password}
        }).then((res)=>{
          if(res.data.err === 0){
            Toast('注册成功');
            this.$router.push('/login')
          }else{
            this.mess = res.data.mess
            console.log(res.data)
          }
        })
      },
    }
  }
</script>

<style scoped>
  .reg-t{display: flex;padding:10px;background:#f7f7f7;border-bottom:1px solid #ccc;}
  .reg-t span{margin-left:40%;font-size:18px;}
  .reg-t b{margin-left:36%;}
  .reg-b p{width:90%;margin-left:5%;}
  .reg-b a{display: block;float:right;padding:10px 10px 0 0;color:#FF3366;}
  span{text-align: center}
</style>