<template>
  <div class="login">
    <div class="login-t">
      <van-icon name="arrow-left" size="24px" tag="i" onclick="window.history.go(-1)"/>
      <span>登录</span>
    </div>
    <div class="login-c">
        <van-field
            v-model="username"
            clearable
            label="用户名:"
            right-icon="question-o"
            placeholder="请输入用户名"
        />
        <van-field
            v-model="password"
            type="password"
            label="密码:"
            placeholder="请输入密码"
        />
    </div>
    <div class="login-b">

      <van-button type="danger" tag="p" @click="login">立即登录</van-button>

      <a @click="reg">注册账号</a>
    </div>
    <span v-model="mess">{{mess}}</span>
  </div>
</template>

<script>
  import { Toast } from 'vant';
  Vue.use(Toast);
  import { Button } from 'vant';
  Vue.use(Button);
  import { Icon } from 'vant';
  Vue.use(Icon);
  import { Field } from 'vant';
  Vue.use(Field);
  import Vue from 'vue'
  export default {
    name: "Login",
    data(){
      return {
        username:'',
        password:'',
        mess:''
      }
    },
    methods:{
      reg(){
        this.$router.push('/reg')
      },
      login(){
        axios({
          url:'/api/login',
          method:'post',
          data:{username:this.username,password:this.password,save:true}
        }).then((res)=>{
          if(res.data.err === 0){
            Toast('登录成功');
            this.$router.push('/user')
          }else{
            this.mess = res.data.mess
            console.log(this.mess)
          }
        })
      }

    }
  }
</script>

<style scoped>
  .login-t{display: flex;padding:10px;background:#f7f7f7;border-bottom:1px solid #ccc;}
  .login-t span{margin-left:40%;font-size:18px;}
  .login-b{}
  .login-b p{width:90%;margin-left:5%;}
  .login-b a{display: block;float:right;padding:10px 10px 0 0;color:#FF3366;}
</style>